.social {
  text-align: center;
  align-items: center;
}

.social__list {
  display: flex;
  list-style: none;
  text-align: center;
}

.social__item {
  width: 48px;
  height: 48px;
  line-height: 48px;
  border: 3px solid #283645;
  border-radius: 50%;
  text-align: center;
  margin-right: 14px;
}
